<template>
    <div class="h">
        <div class="mBg h">
            <mhead-top titleHead="购物车" lfIcon="el-icon-arrow-left icon" lfUrl="back" :rgIcon="login?'el-icon-edit-outline icon':'el-icon-s-home icon'" :rgUrl="login?'/cart/edit':'/home'"></mhead-top>
            <div class="mbg">
                <div class="unLogin" v-if="!login">
                    <p class="f16 mb15 color9">您还尚未登录</p>
                    <el-button class="lBtn" type="primary" @click="toLogin()">登录</el-button>
                </div>
                <div v-else>
                    <div v-if="list.length > 0">
                        <div class="m-c-item" v-for="(item,index) in list" :key="index">
                            <div class="m-c-check">
                                <el-checkbox v-model="item.checked" name="cartBox" @change="handleCheckedChange(item.number,item.cart_id,item.checked)"></el-checkbox>
                            </div>
                            <div class="m-c-pic">
                                <el-image class="mc-image" :src="item.pic"></el-image>
                            </div>
                            <div class="m-c-info">
                                <div class="m-c-name">{{item.name}}</div>

                                <div class="m-c-mid">
                                    <div class="m-c-spec">
                                        规格：{{item.spec_name}}{{item.spec_unit}}
                                    </div>
                                    <div class="mc-price">￥{{item.price}}/{{item.supply_unit}}</div>
                                </div>

                                <div class="m-c-number">
                                    <div class="mc-number">
                                        <el-input-number @change="numberChange(item.number,item.cart_id,item.checked)" size="mini" :min="1" v-model="item.number"></el-input-number>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="countNav">
                            <div class="countCheck">
                                <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                            </div>
                            <div class="countInfo">
                                <div class="c-n-total">合计：￥{{total}}</div>
                                <div class="c-n-confirm" @click="confirm">
                                    <span>下单</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-else class="tc">
                        <img src="../../images/emptyCart.png" height="150" width="150" class="mt30"/>
                        <p class="f16 tc mt30">购物车暂无商品</p>
                        <p class="tc mt30">
                            <router-link :to="{path:'/home'}">
                                <el-button type="primary" plain round>去逛逛</el-button>
                            </router-link>
                        </p>
                    </div>
                </div>
            </div>
        </div>
        <nav-foot menuActive="cart"></nav-foot>
    </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import mheadTop from '../../components/mobile/headTop';
    import navFoot from '../../components/mobile/navFoot';
    import {cartList,changeCartGoodsnum} from '../../service/api'

    export default {
        name: 'mCart',
        data(){
            return{
                checkAll: false,
                isIndeterminate: true,
                list:'',
                total:''
            }
        },
        components:{
            navFoot,
            mheadTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            async initData(){
                if (this.userInfo && this.userInfo.user_id){
                    let res = await cartList();
                    this.list = res.cartInfo;
                    this.total = res.checkedGoodsAmount;
                }
            },
            handleCheckAllChange(val) {
                if(val){
                    //val = true
                    for(var i = 0 ; i < this.list.length ; i++){
                        this.list[i].checked = true;
                    }
                }else{
                    //val = false
                    for(var i = 0 ; i < this.list.length ; i++){
                        this.list[i].checked = false;
                    }
                }
                this.isIndeterminate = false;
                this.calculate();
            },

            handleCheckedChange(number,cartId,isCheck) {
                var checkArr = document.getElementsByName("cartBox");
                var checked_counts = 0;
                for(var i = 0 ; i < checkArr.length; i++){
                    if(checkArr[i].checked){
                        checked_counts++;
                    }
                }
                this.isIndeterminate = checked_counts==checkArr.length? false : true;
                this.checkAll = checked_counts==checkArr.length? true : false;
                this.editCart(number,cartId,isCheck);
                this.calculate();
            },
            async editCart(number,cartId,isCheck){
                let res = await changeCartGoodsnum(number,cartId,isCheck);
            },
            calculate(){
                var total = 0;
                for(var i = 0 ; i < this.list.length ; i++){
                    if(this.list[i].checked){
                        let price = this.list[i].price *100;
                        total +=   parseInt(this.list[i].number)*parseInt(price)/100;
                    }
                }
                this.total = total;
            },
            numberChange(number,cartId,isCheck){
                this.editCart(number,cartId,isCheck);
                this.calculate();
            },
            async confirm(){
                var checkArr = document.getElementsByName("cartBox");
                var checked_counts = 0;
                for(var i = 0 ; i < checkArr.length; i++){
                    if(checkArr[i].checked){
                        checked_counts++;
                    }
                }
                if(checked_counts==0){
                    //未选择任何产品
                    this.$message({
                        message: '您还未选择任何产品',
                        type: 'warning'
                    });
                }else{
                    this.$router.push({ path: '/order/confirm' });
                    //this.$router.push({ name: 'home', params: { userId: wise }})
                }
            },
            toLogin(){
                this.$router.push({ path: '/login/login' });
            }


        },
        created(){

        },
        watch:{
            userInfo: function (value) {
                if (value && value.user_id && !this.list) {
                    this.initData();
                }
            }

        }

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .mbg{
        padding-bottom: px2rem(96);
    }
    .moHead{
        width:100%;
        height: px2rem(90);
        position: fixed;
        top:0;
        left:0;
        background-color:#fafafa;
        border-bottom:1px solid #eee;
        z-index: 1000;
    }
    .mo-nav{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .mo-nav .icon{
        font-size:px2rem(52);
        line-height:px2rem(90);
        padding:0 px2rem(20);
    }
    .mo-edit{
        padding-right:px2rem(20);
        font-size: px2rem(30);
        color:#666;
    }
    .navText{
        height: px2rem(90);
        line-height: px2rem(90);
        font-size: px2rem(36);
    }
    /*未登录*/
    .unLogin{
        width: 100%;
        text-align:center;
        padding-top:px2rem(200);
    }
    .lBtn{
        width:px2rem(400);
        font-size:px2rem(32);
    }
    /*展示*/
    .m-c-item{
        background: #fff;
        padding:px2rem(20);
        margin-top:px2rem(20);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
    }
    .m-c-check{
        width:px2rem(50);
        height: px2rem(180);
        line-height: px2rem(180);
    }
    .m-c-pic{
        width:px2rem(180);
        height: px2rem(180);
        padding-right:px2rem(20);
    }
    .mc-image{
        width:px2rem(180);
        height: px2rem(180);
        object-fit: contain;
        background-color: #f2f2f2;
    }
    .m-c-info{
        width:px2rem(460);
    }
    .m-c-name{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        color:#333;
        line-height: px2rem(36);
        min-width: px2rem(460);
    }
    .m-c-mid{
        display: flex;
        flex-wrap: nowrap;
        margin-top: 5px;
    }
    .m-c-spec{
        font-size:px2rem(24);
        color:#999;
        line-height: px2rem(36);
        width: 100%;
        word-break: break-all;
    }
    .mc-price{
        line-height: px2rem(36);
        color:$colorprice;
        width:100%;
        word-break: break-all;
        text-align: right;
    }
    .m-c-number{
        margin-top:5px;
        font-size:px2rem(24);
        display: flex;
        justify-content: flex-end;
    }


    /*下单Nav*/
    .countNav{
        display: flex;
        flex-wrap:nowrap;
        height: px2rem(96);
        line-height: px2rem(96);
        background-color: #fff;
        position: fixed;
        left:0;
        bottom:px2rem(104);
        width: px2rem(750);
    }
    .countCheck{
        width:px2rem(200);
        padding-left: px2rem(20);
    }
    .countInfo{
        width:px2rem(530);
        display: flex;
        flex-wrap: nowrap;
    }
    .c-n-total{
        width:px2rem(330);
        color:$colorprice;
        text-align: right;
        padding-right: px2rem(20);
    }
    .c-n-confirm{
        background-color: $default;
        width:px2rem(200);
        color:#fff;
        text-align:center;
    }




</style>



